import { getRequest } from "./api";

export const initMenu = (router, store) => {
  if (store.state.routes.length > 0) {
    return;
  }

  getRequest('/home/Sys/sys_menu_config').then(data => {
    // console.log(data);
    if (data) {
      // 把后端得json格式转化成数组格式
      let datas = objToArray(data);

      // 格式化 Router
      // let fmtRoutes = fmtRoutes(datas);
      let fmtRoutes = formatRoutes(datas);
      // 添加到router
      router.addRoutes(fmtRoutes);
      // 将数据存入vuex
      store.commit('initRoutes', fmtRoutes);
    }
  })
}


// 把对象数组转化成普通数组
export const objToArray = (array) => {
  let arr = [];
  for (let i in array) {
    arr.push(array[i]);
    if (array[i].children && array[i].children instanceof Object) {
      let res = [];
      for (let e in array[i].children) {
        res.push(array[i].children[e]);
      }
      array[i]['children'] = res;
    }
  }

  return arr;
}


export const formatRoutes = (routes) => {
  let fmtRoutes = [];
  routes.forEach(router => {
    let {
      path,
      component,
      name,
      hidden,
      iconCls,
      children,
    } = router;
    if (children && children instanceof Array) {
      // 递归
      children = formatRoutes(children);
    }
    let fmtRouter = {
      path: path,
      hidden: hidden,
      name: name,
      iconCls: iconCls,
      children: children,
      component (resolve) {

        if (component.startsWith('Home')) {
          require(['../views/' + component + '.vue'], resolve);
        } else if (component.startsWith('mon')) {
          require(['../views/monitor/' + component + '.vue'], resolve);
        } else if (component.startsWith('user')) {
          require(['../views/user/' + component + '.vue'], resolve);
        } else if (component.startsWith('api')) {
          require(['../views/api/' + component + '.vue'], resolve);
        } else if (component.startsWith('sys')) {
          require(['../views/sys/' + component + '.vue'], resolve);
        } else if (component.startsWith('grade')) {
          require(['../views/grade/' + component + '.vue'], resolve);
        }

      }
    }
    fmtRoutes.push(fmtRouter);
  });
  return fmtRoutes;
}